@import '../libs/variables.scss';
@import '../libs/mixins.scss';

.btn {
	@include button-styles(20px, 10px);

	background: transparent;
	border-radius: 10px;
	transition: all 1s;

	&:hover {
		background-color: rgba(255, 255, 255, 0.3);
		transition: all 0.5s;
		transform: scale(1.2);
	}

	&:active {
		background-color: rgba(255, 255, 255, 0.7);
		transition: all 0.3s;
	}
}

.tooltip {
	font-size: 16px !important;
	font-family: 'Lato', sans-serif !important;
	opacity: 1;

	@media (max-width: 992px) {
		font-size: 14px !important;
	}

	@media (max-width: 767px) {
		font-size: 12px !important;
	}

	@media (max-width: 480px) {
		font-size: 11px !important;
	}
}

.portal {
	@include text-styles(18px, 500, ('Lato', sans-serif));

	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	padding: 15px;
	color: #fff;
	background: #000;
	border-radius: 30px;
	z-index: 2;
	animation: showPortal 0.5s forwards;

	i {
		margin-right: 5px;
	}
}

@keyframes showPortal {
	0% {
		bottom: 0;
	}

	100% {
		bottom: 10%;
	}
}